:root {
  --poster-blue: #0737ff;
  --poster-accent: #0630e6;
  --poster-bg: #fffefa;
  --poster-white: #ffffff;
  --poster-black: #071028;
}
.poster-theme {
  background-color: var(--poster-bg);
  color: var(--poster-black);
  font-family: var(--font-geist-sans), system-ui, -apple-system, "Segoe UI",
    Roboto, "Helvetica Neue", Arial;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: repeating-linear-gradient(
      to right,
      rgba(3, 27, 255, 0.06) 0px,
      rgba(3, 27, 255, 0.06) 1px,
      transparent 1px,
      transparent 64px
    ),
    repeating-linear-gradient(
      to bottom,
      rgba(3, 27, 255, 0.06) 0px,
      rgba(3, 27, 255, 0.06) 1px,
      transparent 1px,
      transparent 64px
    );
  background-size: auto;
}
.hero-section {
  background: var(--poster-blue);
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 192px 24px 40px 24px;
  position: relative;
  overflow: hidden;
}
.hero-section::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: repeating-linear-gradient(
      to right,
      rgba(255, 255, 255, 0.03) 0px,
      rgba(255, 255, 255, 0.03) 1px,
      transparent 1px,
      transparent 80px
    ),
    repeating-linear-gradient(
      to bottom,
      rgba(255, 255, 255, 0.03) 0px,
      rgba(255, 255, 255, 0.03) 1px,
      transparent 1px,
      transparent 80px
    );
  pointer-events: none;
}
.hero-inner {
  text-align: center;
  max-width: 1000px;
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.hero-logo-title {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 32px;
  margin-bottom: 20px;
}
.hero-title-group {
  text-align: left;
}
.hero-main-title {
  font-size: 4.5rem;
  font-weight: 400;
  color: #fff;
  line-height: 1.1;
  letter-spacing: 0.02em;
  margin: 0 0 8px 0;
}
.hero-title-light {
  font-weight: 400;
  color: rgba(255, 255, 255, 0.5);
}
.hero-title-bold {
  font-weight: 800;
  color: #fff;
}
.hero-tagline {
  font-size: 1.8rem;
  color: #fff;
  margin: 0;
  font-weight: 500;
}
.hero-highlight {
  font-style: italic;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.95);
}
.hero-build-line {
  margin-top: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.hero-build-text {
  font-size: 2.2rem;
  color: #fff;
  margin: 0;
  font-weight: 500;
}
.hero-build-text em {
  font-style: italic;
  font-weight: 700;
}
.hero-underline {
  margin-top: -4px;
}
.hero-date {
  font-size: 1.4rem;
  color: rgba(255, 255, 255, 0.85);
  margin-top: 24px;
  font-weight: 600;
}
.hero-cta {
  background: #fff;
  color: var(--poster-blue);
  border: 3px solid #fff;
  padding: 16px 48px;
  font-size: 1.2rem;
  font-weight: 700;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.25s ease;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  text-decoration: none;
  display: inline-block;
}
.hero-cta:hover {
  background: transparent;
  color: #fff;
  transform: translateY(-3px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
}
.hero-powered {
  margin-top: 48px;
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.7);
}
.hero-powered strong {
  color: #fff;
}
@media (max-width: 768px) {
  .hero-logo-title {
    flex-direction: column;
    gap: 16px;
  }
  .hero-title-group {
    text-align: center;
  }
  .hero-main-title {
    font-size: 2.8rem;
  }
  .hero-main-title.hero-title-bold {
    font-size: 3.2rem;
  }
  .hero-tagline {
    font-size: 1.4rem;
  }
  .hero-date {
    font-size: 1.1rem;
  }
  .copilot-logo {
    width: 100px !important;
    height: 100px !important;
  }
}
.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 48px 24px;
}
.hero {
  display: grid;
  grid-template-columns: 1fr;
  gap: 28px;
  align-items: center;
  justify-items: center;
  text-align: center;
  padding: 56px 12px;
}
.hero .title {
  font-size: 6rem;
  line-height: 0.9;
  color: var(--poster-blue);
  font-weight: 800;
  letter-spacing: -0.02em;
  transform: skewX(-8deg);
  text-shadow: none;
}
.hero .subtitle {
  font-size: 1.6rem;
  color: var(--poster-blue);
  margin-top: 8px;
}
.hero .meta {
  margin-top: 18px;
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  justify-content: center;
}
.badge {
  background: var(--poster-blue);
  color: white;
  padding: 6px 12px;
  border-radius: 6px;
  font-weight: 700;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.08) inset;
}
.poster-card {
  background: rgba(7, 55, 255, 0.06);
  padding: 18px;
  border-radius: 8px;
  display: inline-block;
}
.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 18px;
  margin-top: 28px;
}
.info-card {
  background: var(--poster-white);
  border: 3px solid var(--poster-blue);
  padding: 18px;
  border-radius: 6px;
}
.footer {
  margin-top: 48px;
  padding: 28px 0;
  text-align: center;
  color: var(--poster-blue);
}
@media (min-width: 800px) {
  .hero {
    grid-template-columns: 1fr 420px;
    text-align: left;
    justify-items: start;
    gap: 48px;
    align-items: center;
  }
  .hero .title {
    font-size: 8rem;
  }
}
.site-nav {
  position: fixed;
  top: 14px;
  left: 0;
  right: 0;
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: center;
  pointer-events: none;
}
.site-nav-inner {
  background: rgba(255,255,255,0.95);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 3px solid var(--poster-blue);
  padding: 12px 24px;
  border-radius: 999px;
  display: flex;
  gap: 16px;
  align-items: center;
  pointer-events: auto;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}
.nav-mobile-logo {
  display: none;
  font-size: 1.2rem;
  font-weight: 800;
  color: var(--poster-blue);
  text-decoration: none;
  padding: 0;
}
.nav-mobile-logo:hover {
  background: transparent;
}
.nav-links-desktop {
  display: flex;
  gap: 6px;
  align-items: center;
}
.site-nav a {
  color: var(--poster-blue);
  font-weight: 700;
  text-decoration: none;
  padding: 8px 12px;
  border-radius: 6px;
  font-size: 0.9rem;
  transition: background 0.2s ease;
}
.site-nav a:hover {
  background: rgba(7,55,255,0.1);
}
.nav-hamburger {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 8px;
  width: 40px;
  height: 40px;
}
.hamburger-line {
  display: block;
  width: 24px;
  height: 3px;
  background: var(--poster-blue);
  border-radius: 2px;
  transition: all 0.3s ease;
}
.hamburger-line.open:nth-child(1) {
  transform: rotate(45deg) translate(5px, 6px);
}
.hamburger-line.open:nth-child(2) {
  opacity: 0;
}
.hamburger-line.open:nth-child(3) {
  transform: rotate(-45deg) translate(5px, -6px);
}
.nav-mobile-menu {
  display: none;
  flex-direction: column;
  background: rgba(255,255,255,0.98);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 3px solid var(--poster-blue);
  border-radius: 16px;
  padding: 16px;
  margin-top: 10px;
  width: calc(100% - 32px);
  max-width: 400px;
  pointer-events: auto;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.15);
  gap: 8px;
}
.nav-mobile-menu a {
  padding: 12px 16px;
  border-radius: 8px;
  text-align: center;
}
.nav-mobile-menu a:hover {
  background: rgba(7,55,255,0.1);
}
.nav-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: -1;
  pointer-events: auto;
}
@media (max-width: 768px) {
  .site-nav {
    top: 10px;
    padding: 0 16px;
  }
  .site-nav-inner {
    width: 100%;
    padding: 12px 20px;
    border-radius: 50px;
    justify-content: space-between;
  }
  .nav-mobile-logo {
    display: block;
  }
  .nav-links-desktop {
    display: none;
  }
  .nav-hamburger {
    display: flex;
  }
  .nav-mobile-menu.open {
    display: flex;
  }
}
.nav-button-link {
  background: transparent;
  border: none;
  color: var(--poster-blue);
  font-weight: 700;
  padding: 8px 14px;
  border-radius: 6px;
  cursor: pointer;
}
.nav-button-link:hover {
  background: rgba(7,55,255,0.06);
}
.modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(6,10,30,0.45);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  padding: 24px;
}
.modal {
  background: #fff;
  border-radius: 12px;
  padding: 20px 22px;
  max-width: 520px;
  width: 100%;
  box-shadow: 0 12px 40px rgba(6,55,255,0.12);
  text-align: left;
}
.modal-title {
  font-size: 1.4rem;
  color: var(--poster-blue);
  margin: 0 0 6px 0;
}
.modal-body {
  color: #344054;
  margin: 0 0 8px 0;
  line-height: 1.5;
}
.modal-close {
  background: transparent;
  border: none;
  font-size: 1.5rem;
  position: absolute;
  right: 18px;
  top: 18px;
  cursor: pointer;
}
section.site-section {
  padding: 64px 12px;
}
.site-section .section-inner {
  max-width: 1100px;
  margin: 0 auto;
}
:root {
  --rules-bg: #e8ecff;
  --rules-card-bg: #f0f4ff;
  --rules-card-border: #d0d9ff;
  --rules-accent: #0737ff;
  --rules-title: #0737ff;
}
.rules-page-bg {
  background-color: var(--poster-blue) !important;
  background-image: repeating-linear-gradient(
      to right,
      rgba(255, 255, 255, 0.03) 0px,
      rgba(255, 255, 255, 0.03) 1px,
      transparent 1px,
      transparent 80px
    ),
    repeating-linear-gradient(
      to bottom,
      rgba(255, 255, 255, 0.03) 0px,
      rgba(255, 255, 255, 0.03) 1px,
      transparent 1px,
      transparent 80px
    ) !important;
  min-height: 100vh;
  padding: 140px 24px 80px;
}
.rules-container {
  background: #fff;
  border-radius: 12px;
  max-width: 900px;
  margin: 0 auto;
  padding: 40px 48px 48px;
  box-shadow: 0 4px 24px rgba(7,55,255,0.08);
}
.rules-title {
  color: var(--rules-title);
  font-size: 2.4rem;
  font-weight: 700;
  margin: 0 0 8px 0;
  text-align: center;
}
.rules-title-underline {
  height: 3px;
  background: linear-gradient(90deg, var(--rules-accent), var(--rules-card-border));
  border-radius: 2px;
  margin-bottom: 28px;
}
.rules-list {
  display: flex;
  flex-direction: column;
  gap: 18px;
}
.rule-item {
  background: var(--rules-card-bg);
  border: 1px solid var(--rules-card-border);
  border-left: 5px solid var(--rules-accent);
  border-radius: 8px;
  padding: 18px 22px;
}
.rule-item-title {
  color: var(--rules-title);
  font-size: 1.1rem;
  font-weight: 700;
  margin: 0 0 6px 0;
}
.rule-item-text {
  color: #3a3a3a;
  font-size: 0.98rem;
  margin: 0;
  line-height: 1.55;
}
@media (max-width: 640px) {
  .rules-container {
    padding: 28px 18px 32px;
  }
  .rules-title {
    font-size: 1.8rem;
  }
}
.section-title {
  color: var(--poster-blue);
  font-size: 2.8rem;
  font-weight: 800;
  margin: 0 0 18px 0;
  text-align: center;
  line-height: 1.05;
  letter-spacing: -0.02em;
}
@media (min-width: 800px) {
  .section-title {
    font-size: 3.4rem;
  }
}
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 18px;
}
.project-card {
  background: var(--poster-white);
  border: 2px solid var(--poster-blue);
  padding: 14px;
  border-radius: 10px;
  transition: transform .18s ease, box-shadow .18s ease;
}
.project-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 12px 24px rgba(3,27,255,0.08);
}
.project-card .title {
  font-weight: 800;
  color: var(--poster-blue);
  margin-bottom: 6px;
}
.project-card .meta {
  font-size: 0.9rem;
  color: #334155;
}
.projects-section {
  max-width: 1100px;
  margin: 0 auto;
  padding: 20px 20px 60px;
}
.projects-subtitle {
  text-align: center;
  color: #fff;
  font-size: 1.05rem;
  margin: 18px 0 0;
}
.projects-toolbar {
  margin-bottom: 32px;
}
.projects-search-wrapper {
  position: relative;
  max-width: 700px;
  margin: 0 auto 18px;
}
.projects-search-icon {
  position: absolute;
  left: 18px;
  top: 50%;
  transform: translateY(-50%);
  color: #94a3b8;
  pointer-events: none;
}
.projects-search-input {
  width: 100%;
  padding: 14px 18px 14px 50px;
  border: 2px solid #e2e8f0;
  border-radius: 50px;
  font-size: 1rem;
  background: #fff;
  color: #1e293b;
  outline: none;
  transition: border-color 0.2s, box-shadow 0.2s;
}
.projects-search-input:focus {
  border-color: var(--poster-blue);
  box-shadow: 0 0 0 3px rgba(7, 55, 255, 0.1);
}
.projects-search-input::placeholder {
  color: #94a3b8;
}
.projects-filters {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 8px;
}
.projects-filter-btn {
  padding: 7px 18px;
  border: 2px solid #e2e8f0;
  border-radius: 50px;
  background: #fff;
  color: #475569;
  font-size: 0.88rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}
.projects-filter-btn:hover {
  border-color: var(--poster-blue);
  color: var(--poster-blue);
}
.projects-filter-btn.active {
  background: var(--poster-blue);
  color: #fff;
  border-color: var(--poster-blue);
}
.projects-grid-new {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 22px;
}
@media (max-width: 768px) {
  .projects-grid-new {
    grid-template-columns: 1fr;
  }
}
.project-card-new {
  display: block;
  text-decoration: none;
  color: inherit;
